.App {
  text-align: center;
  scroll-behavior: auto;
}

.bg-black-transparant {
  background-color: rgba(0, 0, 0, 0.7);
}

.App-header {
  height: 150px;
  background-color: #282c34;
  flex-direction: column;
  color: white;
  top: 0;
  width: 100%;
  position: fixed;
}

.App-logo {
  height: 55px;
  pointer-events: none;
  float: left;
  margin-left: 50px;
  margin-top: 45px;
}

.App-message {
  font-size: 100px;
  color: white;
  padding-left: 100px;
}

.App-menu {
  text-align: right;
  float: right;
}

.App-menu li {
  font-size: 18px;
  float: right;
  list-style: none;
  list-style-type: none;
  padding-right: 20px;
  margin-right: 60px;
}

.App-menu a{
  color: white;
}

h1 {
  font-size: 60px;
  margin-top: 2%;
  margin-bottom: 2%;
}

.Content-bio {
  font-size: 30px;
}

.Content-story {
  background-color: #000000;
  overflow: hidden;
}

.Content-story-swiper {
  display: flex;
  transition-property: transform;
  will-change: transform;
}

.Content-story-swiper img {
  object-fit: contain;
}

.Content-portfolio {
  text-align: center;
}

.Content-portfolio li, p {
  margin-left: 20%;
  margin-right: 20%;
  font-size: 30px;
  padding-top: 1%;
}

.Content-portfolio h2 {
  font-size: 40px;
  padding-top: 1%;
}

.Content-portfolio img {
  width: 40%;
  margin-top: 1%;
  margin-bottom: 1%;
  margin-left: 20%;
  margin-right: 20%;
}

.image-row {
  margin-top: 1%;
  margin-left: 20%;
  margin-right: 20%;
}

.image-column3 {
  float: left;
  width: 32%;
  padding: 0.5%;
}

.image-column3 img{
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.image-column2 {
  float: left;
  width: 49%;
  padding: 0.5%;
}

.image-column2 img{
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.image-row::after {
  content: "";
  clear: both;
  display: table;
}

.Content-copyright {
  margin-top: 2%;
  margin-bottom: 2%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
